import { Badge, Tabbar, Toast } from "@taroify/core"
import { FriendsOutlined, HomeOutlined, Search, SettingOutlined } from "@taroify/icons"
import * as React from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function BasicTabbar() {
  return (
    <Tabbar>
      <Tabbar.TabItem icon={<HomeOutlined />}></Tabbar.TabItem>
      <Tabbar.TabItem icon={<Search />}></Tabbar.TabItem>
      <Tabbar.TabItem icon={<FriendsOutlined />}></Tabbar.TabItem>
      <Tabbar.TabItem icon={<SettingOutlined />}></Tabbar.TabItem>
    </Tabbar>
  )
}

function KeyTabbar() {
  return (
    <Tabbar defaultValue="1">
      <Tabbar.TabItem value="1" icon={<HomeOutlined />}>
        
      </Tabbar.TabItem>
      <Tabbar.TabItem value="2" icon={<Search />}>
        
      </Tabbar.TabItem>
      <Tabbar.TabItem value="3" icon={<FriendsOutlined />}>
        
      </Tabbar.TabItem>
      <Tabbar.TabItem value="4" icon={<SettingOutlined />}>
        
      </Tabbar.TabItem>
    </Tabbar>
  )
}

function BadgeTabbar() {
  return (
    <Tabbar>
      <Tabbar.TabItem icon={<HomeOutlined />}></Tabbar.TabItem>
      <Tabbar.TabItem badge icon={<Search />}>
        
      </Tabbar.TabItem>
      <Tabbar.TabItem badge="5" icon={<FriendsOutlined />}>
        
      </Tabbar.TabItem>
      <Tabbar.TabItem badge={<Badge content={100} max={99} />} icon={<SettingOutlined />}>
        
      </Tabbar.TabItem>
    </Tabbar>
  )
}

function TabbarWithCustomColor() {
  return (
    <Tabbar className="custom-color">
      <Tabbar.TabItem icon={<HomeOutlined />}></Tabbar.TabItem>
      <Tabbar.TabItem icon={<Search />}></Tabbar.TabItem>
      <Tabbar.TabItem icon={<FriendsOutlined />}></Tabbar.TabItem>
      <Tabbar.TabItem icon={<SettingOutlined />}></Tabbar.TabItem>
    </Tabbar>
  )
}

function EventTabbar() {
  return (
    <>
      <Toast id="toast" />
      <Tabbar onChange={(value) => Toast.open(` ${Number(value) + 1}`)}>
        <Tabbar.TabItem icon={<HomeOutlined />}></Tabbar.TabItem>
        <Tabbar.TabItem icon={<Search />}></Tabbar.TabItem>
        <Tabbar.TabItem icon={<FriendsOutlined />}></Tabbar.TabItem>
        <Tabbar.TabItem icon={<SettingOutlined />}></Tabbar.TabItem>
      </Tabbar>
    </>
  )
}

export default function TabbarDemo() {
  return (
    <Page title="Tabbar " className="tabbar-demo">
      <Block title="">
        <BasicTabbar />
      </Block>
      <Block title=" value ">
        <KeyTabbar />
      </Block>
      <Block title="">
        <BadgeTabbar />
      </Block>
      <Block title="">
        <TabbarWithCustomColor />
      </Block>
      <Block title="">
        <EventTabbar />
      </Block>
    </Page>
  )
}
